html,
body {
  min-height: 100%;
  padding: 0;
  margin: 0;
  font-family: "Open Sans", Frutiger, Helvetica, Arial, sans-serif;
  color: #222;
}

#wrap {
  position: absolute;
  display: flex;
  flex-direction: row;
  width: 100%;
  height: 100%;
}

#content {
  display: flex;
  flex-direction: column;
  flex: 1 1 100%;
  align-items: stretch;
  min-width: 0;
}
#content > div:nth-child(2) {
  flex: 1 1;
}

#main {
  position: relative;
  overflow: auto;
  border: 1px solid rgba(0, 0, 0, 0.08);
  outline: none;
  box-sizing: border-box;
}

#sidebar {
  flex: 1 0 auto;
}

/* © DialogueTrainer */
.ui-widget {
  font-family: inherit !important;
}
.ui-widget input, .ui-widget select, .ui-widget textarea, .ui-widget button {
  font-family: inherit !important;
}

.ui-widget-header {
  border: 0 !important;
}
.ui-widget-header.ui-tabs-nav {
  color: #444;
  background: #EEE !important;
}
.ui-widget-header.ui-tabs-nav .ui-icon {
  background-image: url("vendor/jquery-ui/images/ui-icons_444444_256x240.png");
}

.ui-tabs {
  padding: 0 !important;
}
.ui-tabs .ui-tabs-nav li {
