}

button img {
  vertical-align: middle;
}
#properties button, #sidebar button {
  padding-right: 1px;
  padding-left: 1px;
  font-size: inherit;
  color: #666;
  user-select: none;
}
#toolbar button, #properties button, #sidebar button, #parameters button, #evaluations button, .enumeration-values-container button {
  background: none;
  border: 0;
}

kbd {
  display: inline-block;
  padding: 3px 5px;
  line-height: 10px;
  color: #444D56;
  vertical-align: middle;
  background-color: #FAFBFC;
  border: solid 1px #D1D5DA;
  border-bottom-color: #C6CBD1;
  border-radius: 3px;
  box-shadow: inset 0 -1px 0 #C6CBD1;
}

svg {
  transform: scale(1);
}
svg.icon {
  width: 16px;
  height: 16px;
}

textarea {
  resize: none;
}

a[data-disabled] {
  text-decoration: line-through;
}

.noSelect {
  user-select: none;
}

.clickable:hover {
  cursor: pointer;
}

a.clickable {
  text-decoration: underline;
}

.expander {
  position: absolute;
  width: 1px;
  height: 1px;
}

.hidden {
  display: none !important;
}

.removedParameter {
  display: none;
}

#dragBox {
  position: absolute;
  z-index: 999;
  padding: 3px;
  margin: 0;
  font-size: 12px;
  color: #222;
  pointer-events: none;
  cursor: move;
  background-color: #FAFAFA;
  border: 1px solid rgba(0, 0, 0, 0.25);
  border-radius: 2px;
  box-shadow: 2px 2px 10px rgba(0, 0, 0, 0.1), 1px 1px 2px rgba(0, 0, 0, 0.05);
}
#dragBox.add {
  background-color: #ca505c;
}
#dragBox.error {
  background-color: #ca505c;
}

input[type=checkbox][disabled] {
  color: #CCC;
  opacity: 0.8;
}
input[type=checkbox][disabled] + label {
  color: #CCC;
  opacity: 0.8;
}

.ui-dialog-titlebar.info {
  color: #222;
  background: #40c0ff;
}
.ui-dialog-titlebar.warning {
  color: #222;
  background: #f9bd8b;
}
.ui-dialog-titlebar.error {
  color: #222;
  background: #ef7883;
}

.ui-resizable-n {
  height: 10px;
}

.ui-autocomplete {
  max-height: 135px;
  overflow-x: hidden;
  overflow-y: auto;
}

.ui-tooltip-content {
  font-size: 10pt;
}

tr.ui-sortable-helper {
  display: table;
}

.buttonGroup {
  margin: 1px;
  word-wrap: break-word;
  vertical-align: top;
}

.markdown-tooltip {
  position: relative;
  display: none;
  height: 0;
  text-align: right;
}
.markdown-tooltip > span {
  display: inline-block;
  padding: 5px;
  line-height: 0;
  background: rgba(255, 255, 255, 0.95);
  border: 1px solid rgba(0, 0, 0, 0.25);
  border-radius: 3px;
  box-shadow: 0 1px 2px rgba(0, 0, 0, 0.15);
}
.markdown-tooltip svg {
  width: 10px;
  height: 10px;
  pointer-events: none;
}
textarea:focus + .markdown-tooltip {
  display: block;
}
input:focus + .markdown-tooltip {
  position: relative;
  z-index: 1;
  display: inline-block;
  width: 0;
  transform: translate(3px, 0);
}

.markdownUITooltip code {
  white-space: nowrap;
}

.enumeration-values-container li {
  clear: both;
}
.enumeration-values-container span,
.enumeration-values-container input {
  float: left;
}
.enumeration-values-container button {
  float: right;
}

#statement,
#comment,
#treeNameBox {
  display: block;
  width: 100%;
  box-sizing: border-box;
}

.iconButton {
  cursor: pointer;
}
.iconButton svg {
  width: 16px;
  height: 16px;
  margin: 3px 5px 3px 0;
  fill: currentColor;
  vertical-align: middle;
}
.iconButton.add svg {
  fill: #0080BF;
}
.iconButton.delete svg {
  fill: #CE192A;
}

.handle {
  cursor: move;
  cursor: grab;
  user-select: none;
}
.ui-sortable-helper .handle {
  cursor: move;
  cursor: grabbing;
}
.parameter-effect .handle {
  padding: 0 5px;
}
.condition .handle {
  padding-right: 5px;
}

select {
  position: relative;
}
select:focus {
  z-index: 1;
}

.evaluation-type {
  width: 80%;
}
.evaluation-type ~ .define-type {
  width: 20%;
  padding: 0;
}

.ui-dialog-content select,
.ui-dialog-content input,
.ui-dialog-content textarea {
  max-width: 100%;
}

.indicator {
  position: absolute;
  right: -25px;
  bottom: -25px;
  padding: 3px 5px;
  margin: 0 5px 5px 0;
  color: white;
  background-color: #3892FF;
  border: 1px solid #3892FF;
  border-radius: 3px;
  box-shadow: 0 2px 1px rgba(0, 0, 0, 0.07);
}

.indent {
  margin-left: 20px;
}

.listType {
  padding-inline-start: 0;
}
.listType li {
  display: flex;
  align-items: center;
  margin-bottom: 0.25em;
  gap: 5px;
}
.listType li::before {
  content: "•";
  display: inline;
}
.listType li .control {
  flex-grow: 1;
}
.listType.string li {
  align-items: flex-start;
}

/* © DialogueTrainer */
.zoomTreeButton {
  width: 50px;
  font-size: 0;
  text-align: center;
  cursor: pointer;
}
.zoomTreeButton svg {
  width: 32px;
  height: 32px;
  fill: currentColor;
  vertical-align: middle;
}

